<template>
  <div class="home container">
    <div class="row message">
      <div class="col-xs-12">{{ message }}</div>
    </div>
    <div class="row">
      <div class="col-xs-3">
        <input type="text" v-model="registerFirstName" ref="registerFirstName" placeholder="First Name" class="form-control register-input"><br />
        <input type="text" v-model="registerLastName"ref="registerLastName" placeholder="Last Name" class="form-control register-input">
        <button type="submit" v-on:click="register" class="btn btn-primary btn-block" style="margin-top: 15px;">Sign Up</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'home',
  data () {
    return { 
      registerFirstName: '',
      registerLastName: '',
      message: ''
    }
  },
  methods: {
    register: function() {
        this.message = '';

        if (this.registerFirstName === '') {
          this.message = 'Please enter your First Name';
          this.$refs.registerFirstName.focus();
          return;
        }

        if (this.registerLastName === '') {
          this.message = 'Please enter your Last Name';
          this.$refs.registerLastName.focus();
          return;
        }
        
        this.$http.post('http://localhost:3903/api/home/Register', { 'FirstName': this.registerFirstName, 'LastName': this.registerLastName }).then(response => {
          this.$router.push('members');
        });
    }
  }
}
</script>

<style scoped>
.message { height:22px; color:#ff0000; margin-bottom: 5px;}
</style>



