<template>
    <div class="members container">
        <ul>
            <li v-for="member in members">
            {{ member.FirstName + ' ' + member.LastName }}
            <button v-on:click="deleteMember(member.Id)">Delete</button>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    data: () => ({
        members: []
    }),

    created() {
        this.$http.get('http://localhost:3903/api/home/GetMembers').then(response => {
            this.members = response.data
        });
    },

    methods: {
        deleteMember(id) {
            this.$http.post('http://localhost:3903/api/home/DeleteMember/', { id: id }).then(response => {
                for (var i = 0; i < this.members.length; i++) {
                    if (this.members[i].Id === id) {
                        this.members.splice(i, 1);
                    }
                }
            });
        }
    }
}
</script>


